<template>
  <div class="sign">
    <div class="sign__header clearfix">

    </div>
    <h1 class="sign__title">
      开户成功
    </h1>
    <p v-if="!isFast" class="sign__tips">
      您可以使用手机号发送验证码或账号密码登录
    </p>
    <form @submit.prevent class="form">
      <div class="form__item">
        <div class="form__content">
          <label class="form__label--prefix" for="sign_up_acc">账户ID：</label>
          <input v-model.trim="loginName" :readonly="readonlyAcc" ref="acc"
                 class="form__input sign-up-form__input" id="sign_up_acc"
                 placeholder="c开头5-10位数字或字母" maxlength="12" type="text">
          <button v-if="readonlyAcc===true" @click="toggleAcc" class="form__suffix form__suffix-button border">
            点击修改
          </button>
          <button v-else-if="readonlyAcc===false" @click="updateAcc" class="form__suffix form__suffix-button border">
            确定
          </button>
        </div>
      </div>
      <div class="form__item">
        <div class="form__content">
          <label class="form__label--prefix" for="sign_up_pwd">密码：</label>
          <input v-model.trim="password" :readonly="readonlyPwd" ref="pwd"
                 class="form__input sign-up-form__input" id="sign_up_pwd"
                 placeholder="6-16位数字及字母组成" maxlength="16" type="text">
          <button v-if="readonlyPwd===true" @click="togglePwd" class="form__suffix form__suffix-button border">
            点击修改
          </button>
          <button v-else-if="readonlyPwd===false" @click="updatePwd" class="form__suffix form__suffix-button border">
            确定
          </button>
        </div>
      </div>
      <div v-if="isFast" class="form__item">
        <label class="form__label" for="sign_in_parent">
          好友推荐码（选填）
        </label>
        <div class="form__content form__content--suffix">
          <input v-model.trim="parentId" :readonly="readonlyParentId"
                 class="form__input sign-up-form__input" id="sign_in_parent"
                 placeholder="推荐码由10位数字组成" maxlength="10" type="text">
          <button v-if="!readonlyParentId" @click="updateParentId" class="form__suffix form__suffix-button border">
            确定提交
          </button>
        </div>
      </div>
      <h3 class="newbie-offer">
        新会员专享，<a class="newbie-offer__link c-primary" href="javascript:void (0);">存款5重好礼</a>，最高赠送礼金5,000元。
      </h3>
      <button @click="()=>null" class="form__button form__button--full">
        立即存款
      </button>
      <button @click="$router.replace('/')" class="form__button form__button--empty border">
        进入首页
      </button>
    </form>
    <p class="sign__notes sign-up-success__notes">
      凯发娱乐支持USDT <span class="sign__notes-strong">安全 • 方便</span> 匿名买卖、不受银行监管
    </p>
  </div>
</template>

<script>
  import { mapActions } from 'vuex'
  import { validateLoginName, validatePassword, validateParentId } from '@/utils/validate.js'

  export default {
    name: 'sign_up_success',
    beforeRouteEnter(to, from, next) {
      if (to.params.loginName && to.params.password) {
        next()
      } else {
        next('/')
      }
    },
    data() {
      return {
        loginName: null,
        password: null,
        parentId: null,
        readonlyAcc: true,
        readonlyPwd: true,
        readonlyParentId: false,
        isFast: false
      }
    },
    computed: {
      _password() {
        return this.readonlyPwd === 'readonly'
          ? this.password
          : this.$route.params.password
      }
    },
    created() {
      this.isFast = this.$route.params.isFast
      this.password = this.$route.params.password
      this.loginName = this.$route.params.loginName
    },
    methods: {
      ...mapActions([
        'login',
        'getCustomerInfo'
      ]),
      toggleAcc() {
        this.readonlyAcc = false
        this.$refs.acc.focus()
      },
      togglePwd() {
        this.readonlyPwd = false
        this.$refs.pwd.focus()
      },
      updateAcc() {
        let params = {
          type: 1,
          password: this.$encrypted(this._password),
          userAccount: this.loginName
        }
        if (validateLoginName(this.loginName)) {
          this.$api.modifyTempLoginName(params).then(({ head, body }) => {
            if (head['errCode'] === '0000') {
              this.readonlyAcc = 'readonly'
              return this.login({
                loginName: body.loginName,
                verifyStr: this._password
              })
            }
          }).then(({ head }) => {
            if (head && head['errCode'] === '0000') {
              this.$toast.success('修改成功')
            }
          }).catch(e => console.log(e))
        } else {
          this.$toast.error('请输入c开头5-11位数字或字母')
        }
      },
      updatePwd() {
        let params = {
          newPassword: this.$encrypted(this.password),
          oldPassword: this.$encrypted(this.$route.params.password)
        }
        if (validatePassword(this.password)) {
          this.$api.modifyPwd(params).then(({ head }) => {
            if (head['errCode'] === '0000') {
              this.readonlyPwd = 'readonly'
              this.$toast.success('修改成功')
            }
          })
        } else {
          this.$toast.error('密码由6-16位数字及字母组成')
        }
      },
      updateParentId() {
        let params = {
          changeParentId: 1,
          parentId: this.parentId
        }
        if (validateParentId(this.parentId)) {
          this.$api.modify(params).then(({ head }) => {
            if (head['errCode'] === '0000') {
              this.readonlyParentId = true
              this.$toast.success('修改成功')
            }
          })
        } else {
          this.$toast.error('推荐码由10位数字组成')
        }
      }
    }
  }
</script>

